'use client'

import { cn } from "@/lib/utils";

type Variant = {
    size: string;
    value: string;
    disabled?: boolean;
}
interface Props {
    items: readonly Variant[];
    onClick?: (value: Variant['value']) => void;
    selectedValue?: Variant['value'];
    className?: string;
}

export const VariantsSizes: React.FC<Props> = ({ items, onClick, className, selectedValue }) => {
    return(
        <div className= {cn( className,'size__items')}>
            {items.map((item) => (
                <button 
                    key={item.value} 
                    onClick={() => onClick?.(item.value)}
                    className={cn(
                        "size__item",
                        {
                            "size__item_active": item.value === selectedValue,
                            "pointer-events-none": item.disabled,
                        }
                    )}>
                    {item.size}
                </button>
            ))}
        </div>
    )
}
    