"use client";

import { cn } from "@/lib/utils";
import { Category } from "@prisma/client";
import React, { useState } from "react";
import { VariantsSizes } from "./variants-sizes";

interface Props {
    imageUrl: string;
    alt: string;
    name: string;
    price?: number;
    oldPrice?: number | null,
    items?: any[];
    onClickAdd?: VoidFunction;
    className?: string;
}

export const ChoiceProduct: React.FC<Props> = ({  
    imageUrl, 
    alt,
    name,
    price,
    oldPrice,
    items,
    onClickAdd,
    className 
}) => {

    return (
        <section className={cn(className,"product-item center")}>
            <div className="product-item__block-image">
                <img src={imageUrl} alt={alt} className="block-image__image" />
            </div>
            <div className="product-item__block-options">
                <div className="block-options__box-price">
                    <p className="block-options__price">{price}</p>
                    {oldPrice ? <p className="block-options__price_old">{oldPrice}</p> : null}
                </div>
                <form action="#" className="block-options__size">
                    <p className="size__title">Выберите размер</p>
                    <VariantsSizes items={co}/>
                    {/* <div className="size__items">
                        <button type="button" className="size__item">{productItem?.size}</button>
                        <button type="button" className="size__item size__item_active">M</button>
                        <button type="button" className="size__item">L</button>
                        <button type="button" className="size__item">XL</button>
                        <button type="button" className="size__item">XXL</button>
                    </div> */}
                </form>
                <form action="#" className="block-options__color">
                    <p className="color__title">Выберите цвет</p>
                    <div className="color__items">
                        <button type="button" className="color__item color__item_1"></button>
                        <button type="button" className="color__item color__item_2 color__item_active"></button>
                        <button type="button" className="color__item
                        color__item_3"></button>
                        <button type="button" className="color__item
                        color__item_4"></button>
                    </div>
                </form>
                <div className="block-options__btns">
                    <button className="block-options__quantity">1</button>
                    <div className="block-options__to-cart">Добавить в корзину</div>
                </div>
            </div>
        </section>
    );
};
